<template>
  <section>
    <b-container class="shop__featured-bg my-4 py-1">
      <h2 class="font-weight-bolder py-2">
        Featured Products for You
      </h2>
      <b-row>
        <b-col
          v-for="product in products"
          :key="product.id"
          md="3"
          cols="12"
        >
          <b-card
            class="ecommerce-card"
            no-body
          >
            <div class="item-img text-center">
              <b-link :to="{
              name: 'apps-e-commerce-product-details',
              params: { slug: product.slug },
            }">
                <b-img
                  :alt="`${product.name}-${product.id}`"
                  fluid
                  class="card-img-top"
                  :src="product.image"
                />
              </b-link>
            </div>

            <!-- Product Details -->
            <b-card-body>
              <div class="item-wrapper">
                <div class="item-rating">
                  <ul class="unstyled-list list-inline">
                    <li
                      v-for="star in 5"
                      :key="star"
                      class="ratings-list-item"
                      :class="{ 'ml-25': star - 1 }"
                    >
                      <feather-icon
                        icon="StarIcon"
                        size="16"
                        :class="[
                      { 'fill-current': star <= product.rating },
                      star <= product.rating ? 'text-warning' : 'text-muted',
                    ]"
                      />
                    </li>
                  </ul>
                </div>
                <div>
                  <h6 class="item-price">${{ product.price }}</h6>
                </div>
              </div>
              <h6 class="item-name">
                <b-link
                  class="text-body"
                  :to="{
                name: 'apps-e-commerce-product-details',
                params: { slug: product.slug },
              }"
                >
                  {{ product.name }}
                </b-link>
                <b-card-text class="item-company">
                  By
                  <b-link class="ml-25">
                    {{ product.brand }}
                  </b-link>
                </b-card-text>
              </h6>
              <b-card-text class="item-description">
                {{ product.description }}
              </b-card-text>
            </b-card-body>

            <!-- Product Actions -->
            <div class="item-options text-center">
              <b-button
                variant="light"
                tag="a"
                class="btn-wishlist w-50 bg-transparent border-none"
                @click="toggleProductInWishlist(product)"
              >
                <feather-icon
                  icon="HeartIcon"
                  class="mr-50"
                  :class="{ 'text-danger': product.isInWishlist }"
                />
                <span>Wishlist</span>
              </b-button>
              <b-button
                variant="primary"
                tag="a"
                class="btn-cart w-50"
                @click="handleCartActionClick(product)"
              >
                <feather-icon
                  icon="ShoppingCartIcon"
                  class="mr-50"
                />
                <span>{{ product.isInCart ? "View In Cart" : "Add to Cart" }}</span>
              </b-button>
            </div>
          </b-card>
        </b-col>
      </b-row>
    </b-container>
  </section>
</template>

<script>
import {
  BCard,
  BCardText,
  BButton,
  BImg,
  BCardBody,
  BLink,
  BRow,
  BCol,
  BContainer,
} from 'bootstrap-vue'
import Ripple from 'vue-ripple-directive'
import { useShopRemoteData } from '../useECommerceShop'

export default {
  components: {
    BCard,
    BCardText,
    BButton,
    BCardBody,
    BLink,
    BImg,
    BRow,
    BCol,
    BContainer,
  },
  directives: {
    Ripple,
  },
  data() {
    return {
      // contentWidth: this.$store.state.appConfig.layout.contentWidth,
      menuHidden: this.$store.state.appConfig.layout.menu.hidden,
      value3: 67,
    }
  },
  setup() {
    const { products, fetchProducts } = useShopRemoteData()
    // Wrapper Function for `fetchProducts` which can be triggered initially and upon changes of filters
    const fetchShopProducts = () => {
      fetchProducts({
        q: '',
        sortBy: '',
        page: 1,
        perPage: 4,
      }).then(response => {
        products.value = response.data.products
      })
    }

    fetchShopProducts()
    return {
      // useShopRemoteData
      products,
    }
  },
  updated() {
    // this.$store.commit('appConfig/UPDATE_CONTENT_WIDTH', 'boxed')
  },
  created() {
    // this.$store.commit('appConfig/UPDATE_CONTENT_WIDTH', 'boxed')
    this.$store.commit('appConfig/UPDATE_NAV_MENU_HIDDEN', true)
  },
  destroyed() {
    this.$store.commit('appConfig/UPDATE_CONTENT_WIDTH', this.contentWidth)
    this.$store.commit('appConfig/UPDATE_NAV_MENU_HIDDEN', this.menuHidden)
  },
}
</script>
<style lang="scss" scoped>
.item-description {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.item-company {
  display: none;
}
.item-options {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
}
.btn-cart {
  border-radius: 0;
}
.btn-wishlist {
  border-radius: 0;
  background-color: #161d31 !important;
  border-color: transparent;
  color: white;
}
.ecommerce-card {
  overflow: hidden;

  .card-body {
    padding: 1rem;
  }
}
.shop__featured-bg {
  background-size: cover;
  background-color: rgba(115, 103, 240, 0.12) !important;
}
</style>
